import '@stoplight/elements/styles/elements.css';
import '../styles/stoplight.css';

import { PageProps } from 'gatsby';
import React from 'react';

import Layout from '../components/layout';
import SEO from '../components/seo';
import StoplightPage from '../components/stoplight-page';

const GuidesPage = ({ location }: PageProps) => {
  const nodeUri = location.pathname.replace('/guides/', '') || '/docs/introduction.md';

  return (
    <Layout centered={false}>
      <SEO title="Guides" />

      <StoplightPage path="guides" projectSrn="gh/stoplightio/studio-demo" nodeUri={nodeUri} />
    </Layout>
  );
};

export default GuidesPage;
